'use strict';

function Model(data) {
	var self = this;
	self.data = data.slice();
	self.addItem = function (item) {
		// alert ('addItem');
		if (item.length === 0) {
			return;
		}
		self.data.push(item);
		return self.data;
	};
	self.removeItem = function (index) {
		// alert ('removeItem');
		if (index === -1) {
			return;
		}
		self.data.splice(index, 1);
		// alert (self.data.length);
		return self.data;
	};
}
/////////////////////////////////////////////////////////////////
function View(model) {
	var self = this;
	function init() {
		self.renderList(model.data);
	}

	self.renderList = function (data) {
		var List = _.template($('#table-template').html());
		var content = List({ 'data': data });
		$('.tbody').html(content);
		// alert ('renderList');
	};
	init();
}
function Controller(model, view) {

	delBtn.addEventListener('click', removeItem);
	// $('.item-add').on('click',addItem);
	$('.delBtn').on('click', removeItem);
	function addItem() {
		var newItem = $('.item-value').val();
		model.addItem(newItem);
		view.renderList(model.data);
		$('.item-value').val('');
	}
	function removeItem() {
		var index = this.getAttribute('data-value') - 1;
		alert('removeItem');
		model.removeItem(index);
		view.renderList(model.data);
	}
}
